class FormControl {
  constructor() {
    this.data = {};
    this.form = document.getElementById('form');
    this.isSubmitted = false;

    this.init();
  }

  init() {
    this.form.addEventListener('submit', (e) => this.handleSubmit(e));
  }

  handleSubmit(e) {
    e.preventDefault();
    if (this.isSubmitted) {
      return;
    }

    this.isSubmitted = true;
    this.lockForm();

    const elements = e.currentTarget.elements;

    this.data.email = elements.email.value;
    this.data.firstName = elements.firstName.value;
    this.data.lastName = elements.lastName.value;
    this.data.info = elements.info.value;

    console.log(this.data);
  }

  lockForm() {
    this.form.classList.add('form--disabled');
    this.form.querySelector('button').classList.add('btn--disabled');
  }

  unlockForm() {
    this.form.classList.remove('form--disabled');
    this.form.querySelector('button').classList.remove('btn--disabled');
  }
}

export default new FormControl();
